import React, { useState } from 'react';
import axios from 'axios';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import 'mdb-react-ui-kit/dist/css/mdb.min.css';
import { NumericFormat } from "react-number-format";
import { MDBInput, MDBCol, MDBContainer, MDBRow, MDBCard, MDBCardText, MDBCardBody, MDBBtn , MDBCardTitle } from 'mdb-react-ui-kit';
import Moment from 'react-moment';
import './App.css';

function App() {
  const [search, setSearch] = useState("");
  const [errorMessage, setErrorMessage] = useState("");
  const [wUser, setUser] = useState({});
  const apikey = process.env.REACT_APP_API_KEY
  function getUser() {
    //www.last.fm/api/show/user.getInfo
    let url = "https://ws.audioscrobbler.com/2.0/?method=user.getinfo&user=" + search + "&api_key=" + apikey + "&format=json";
    axios.get(url)
      .then(function (response) {
        console.log(response.data.user)
        setUser(response.data.user);
      })
      .catch(function (error) {
        setErrorMessage(error.message);
        toast.error("Error: " + error.message);
      });
  }

  return (
    <div className="App">
      <div className="Container">
        <h1>last.fm search</h1>
        <MDBInput label='Enter User' type='text' value={search} onChange={e => setSearch(e.target.value)} contrast onKeyDown={(e) => {if(e.keyCode === 13) getUser()}}/>
<br/>
<MDBBtn className="btn1" color='dark' rounded onClick={getUser}>Search</MDBBtn>

      </div>
     
      {
  JSON.stringify(wUser) !== "{}" ?
  <MDBContainer>
  <MDBRow className="justify-content-center">
    <MDBCol>
      <MDBCard className="my-5 user-card" style={{position: 'relative'}}>
        <MDBCardBody className="text-center user-details">
            <img src={wUser.image[2]["#text"]} onError={(e) => {e.target.onerror = null; e.target.src="http://via.placeholder.com/640x360"}} alt="Profile"  className="rounded-circle img-thumbnail" />
            <MDBCardTitle className="mt-4">{wUser.name}</MDBCardTitle>
            <MDBCardText>Registered: <Moment format="DD MMM YYYY" unix>{wUser.registered.unixtime}</Moment></MDBCardText>
            <MDBRow className="justify-content-around">
              <MDBCol>
                <MDBCardText>Scrobbles</MDBCardText>
                <MDBCardText><NumericFormat 
                value={wUser.playcount} 
                displayType={'text'} 
                thousandSeparator={true} 
              /></MDBCardText>
              </MDBCol>
              <MDBCol>
                <MDBCardText>Tracks</MDBCardText>
                <MDBCardText><NumericFormat 
                value={wUser.track_count} 
                displayType={'text'} 
                thousandSeparator={true} 
              /></MDBCardText>
              </MDBCol>
              <MDBCol>
                <MDBCardText>Albums</MDBCardText>
                <MDBCardText><NumericFormat 
                value={wUser.album_count} 
                displayType={'text'} 
                thousandSeparator={true} 
              /></MDBCardText>
              </MDBCol>
            </MDBRow>
        </MDBCardBody>
      </MDBCard>
    </MDBCol>
  </MDBRow>
</MDBContainer>
  : 
  <></>
}
          <ToastContainer
          position="top-right"
          autoClose={5000}
          hideProgressBar={false}
          newestOnTop={false}
          closeOnClick
          rtl={false}
          pauseOnFocusLoss
          draggable
          pauseOnHover
          theme="dark" />
    </div>
  );
}

export default App;
